<template>
  <div id="app">
    <Menu :menuOpenStatus="menu_status" :nowstatus="menu_now_status" v-on:menuOpenStatus="getMenuStatus" v-on:nowStatus="getMenuNowStatus" />
    <div class="fullpage" :class="menu_status">
      <Header :menuOpenStatus="menu_status" :userOpenStatus="user_status"  :noticeOpenStatus="notice_status"
      v-on:menuStatus="getMenuStatus" v-on:userStatus="getUserStatus" v-on:noticeStatus="getNoticeStatus"/>
      <Home msg="AORUS Sales APP" :nowstatus="menu_now_status" :noticestatus="notice_status" :userstatus="user_status" v-on:noticeStatus="getNoticeStatus" v-on:userStatus="getUserStatus"/>
    </div>
    <SubPage/>
  </div>
</template>

<script>
import Header from './components/commit/Header'
import Home from './components/Home'
import Menu from './components/commit/Menu'
import SubPage from './components/SubPage'


export default {
  name: 'App',
  components: {
    Header,
    Home,
    Menu,
    SubPage
  },
  mounted:function(){
  },
  data(){
    return{
      menu_status: "", //active 預設開啟MENU
      user_status: false,
      notice_status: false,
      menu_now_status: "2", // 1-6 預設開啟頁面,
    }
  },
  methods: {

    getMenuStatus: function (data) {
      this.menu_status = data
    },
    
    getUserStatus: function (data) {
      this.user_status = data
    },

    getNoticeStatus: function (data) {
      this.notice_status = data
    },

    getMenuNowStatus: function(data){
      this.menu_now_status = data
    },
  }
}
</script>

<style scoped src="@/assets/css/style.css"></style>
<style>
body{
  margin: 0 auto;
  background: #000;
  color: #fff;
  overflow: hidden;
}
#app{
  font-family: 'Microsoft JhengHei',sans-serif;
  position: relative;
  margin: 0 auto;
  width: 100%;
  /* overflow-x: hidden; */
}
.fullpage{
  position: relative;
  margin: 0;
  width: 100%;
  transition: 0.3s all;
  -webkit-transform: translateZ(0);
  -moz-transform: translateZ(0);
  -ms-transform: translateZ(0);
  -o-transform: translateZ(0);
  transform: translateZ(0);
}
.fullpage.active{
  position: relative;
  margin: 0 auto;
  width: 100%;
  margin-left: 250px;
  width: calc(100% - 250px);
}



@media (max-width: 990px){
  .fullpage.active{
    position: absolute;
    width: 100vw;
    margin-left: 300px;
    left: 0;
  }

}
</style>
