<template>
  <div class="container-fluid">
    <div class="row pb-3">
      <div class="col-12 mt-3">
        <h2>Customer Profile</h2>
      </div>
      
      <div class="col-12 col-sm-12 col-md-6 col-lg-6 col-xl-4">
        <div class="box">
          <div class="content">
            <canvas id="chartCustomerProfile"></canvas>
          </div>
          <div class="mt-0" style="font-size: 10px;color: rgba(255,255,255,0.2)">點擊圖表顯示該區資訊</div>
        </div>
      </div>
      <div class="col-12 col-sm-12 col-md-6 col-lg-6 col-xl-8">
        <div class="box">
          <div class="title">
            {{customerProfileData[customerProfileDataId].name}}
          </div>
          <div class="content">
            <table class="customerTable">
              <thead>
                <tr>
                  <th style="text-align: center">處級名稱</th>
                  <th style="text-align: center">全球實銷佔比</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(key,index) in customerProfileData[customerProfileDataId].tableData" :key="index" @click="openCustomerProfileSubPage(index)">
                  <td align="center">{{key.name}}</td>
                  <td align="center">{{key.value}}</td>
                </tr>
              </tbody>
            </table>
          </div>
          <div class="mt-0" style="font-size: 10px;color: rgba(255,255,255,0.2)">點擊表格顯示詳細資訊</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Chart from 'chart.js'

var _chart01;

export default {
  props: {
  },
  mounted:function(){ 
    this.init() 
  },
  computed: {
    customerProfileData() {
      return this.$store.state.customerProfileData;
    },
    customerProfileDataId(){
      return this.$store.state.customerProfileDataId
    }
  },
  data(){
    return {
      chart01Data : [
        ['APEC','EU','NALA','中嘉'],
        ['20.02','33.89','21.2','24.89']
      ],
    }
  },
  methods:{
    init(){
      this.setChart();
    },
    setChart(){
      if(_chart01){
        _chart01.destroy();
      }
      
      var ctx01 = document.getElementById('chartCustomerProfile');
      _chart01 = new Chart(ctx01, {
          type: 'doughnut',
          data: {
              labels: this.chart01Data[0],
              datasets: [{
                  label: this.chart01Data[0],
                  data: this.chart01Data[1],
                  backgroundColor: [
                      '#ff6384'
                      ,'#ffcd56'
                      ,'#36a2eb'
                      ,'#66c9c9'
                  ],
                  borderColor: [
                      '#ff6384'
                      ,'#ffcd56'
                      ,'#36a2eb'
                      ,'#66c9c9'
                  ],
                  borderWidth: 0,
              }],
          },
          options: {
            aspectRatio: 2,
            // cutoutPercentage: 50
            legend: {
              labels: {
                  width: 50,
                  fontColor: '#fff',
                  fontFamily: 'Microsoft JhengHei'
              },
              position : "left"
            },
            // title: {
            //     display: false,
            //     text: 'Custom Chart Title'
            // },
            // layout: {
            //     padding: {
            //         left: 0,
            //         right: 0,
            //         top: 0,
            //         bottom: 0
            //     }
            // }
          }
      });
      let _this = this;
      ctx01.onclick = function(e) {
        var slice = _chart01.getElementAtEvent(e);
        if (!slice.length) return; // return if not clicked on slice
        _this.$store.state.customerProfileDataId = slice[0]._index;
        
      }
    },
    openCustomerProfileSubPage(customerProfileSubPageId){
      console.log(this.$store.state.customerProfileData[this.$store.state.customerProfileDataId].pageData[customerProfileSubPageId]);
      
      if(this.$store.state.customerProfileData[this.$store.state.customerProfileDataId].pageData[customerProfileSubPageId].length != 0){
        this.$store.state.customerProfileSubPageId = customerProfileSubPageId;
        this.$store.commit('openSubPage');
      }
        
      //log 目前的狀態
      // this.$store.commit('logAllStatus');

    }
  }
}
</script>

